.main__banner {
    position: absolute;
    top: 0;
    z-index: -2;
    &::before {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        z-index: 1;
        border-left: 0px solid transparent;
        border-right: 477px solid transparent;
        border-top: 288px solid map-get($mau, main);
    }
    img {
        width: 100%;
        height: 1080px;
    }
}